import React from 'react';
import Eatlas from 'eatlas';

import './App.css';
/**
 * Strings as placeholders to be replaced
 * in R.
 * 
 * defaultURL, tooltipColumns, geographyURL,
 * geographyColumn, column, data, layerName, dark,
 * viewport, hideChartGenerator,
 * hideCharts
 * 
 * 
 * @returns Eatlas instance
 */
function App() {
  return (
    <Eatlas 
      defaultURL={"tgver.defaultURL"}
      tooltipColumns="tgver.tooltipColumns"
      geographyURL="tgver.geographyURL"
      geographyColumn="tgver.geographyColumn"
      column="tgver.column"
      data="tgver.data"
      layerName="tgver.layerName"
      dark="tgver.dark"
      viewport="tgver.viewport"
      hideChartGenerator="tgver.hideChartGenerator"
      hideCharts="tgver.hideCharts"/>
  );
}

export default App;
